 @import url('https://fonts.googleapis.com/css?family=Pacifico|Roboto|Raleway'); 
 @import url('https://fonts.googleapis.com/css?family=Lobster|Rubik|Bree+Serif');
 @import url('https://fonts.googleapis.com/css?family=Dancing+Script|Cookie');
 @import url('https://fonts.googleapis.com/css?family=Slabo+27px');


body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  max-width: 100%;
  position: relative;
  overflow-x: hidden;

  -webkit-columns: #000000;

     -moz-columns: #000000;

          columns: #000000;
  background-image: url("./static/images/bg/bg4.svg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  /* FONT */
  font-family:Raleway;
  font-size: 16px;
  color: white;
  text-shadow: 2px 2px 4px #000000;
  -webkit-text-size-adjust: 100%;
}
.full-vh {
  height: 134vh;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* PRELOADER CSS */
.preloader-active {
  overflow-y: hidden;
  overflow-x: hidden;
}

#preloader {
  height: 100vh;
  width:100vw;
  background-color: #ffffff;
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 9999;
}
#preloader > img{
  margin:auto;
}

/*BODY CSS WIDTH 90%*/
.site-body{
  width:90%;
  margin: 0 auto;
  display: block;
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
  overflow: auto;
  min-height:100vh;
  position: relative;
  padding-top: 70px;
  padding-bottom: 150px;
  z-index: 0;
}


/*BODY CSS WIDTH 90%*/
.site-body-full{
  width:100%;
  margin: 0 auto;
  display: block;
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
  overflow: auto;
  min-height:100vh;
  position: relative;
  padding-top: 70px;
  padding-bottom: 150px;
  z-index: 0;
}

/* FOOTER CSS */
.site-footer{
  bottom:0px;
  padding: 10px 30px;
  position: absolute;
  background-color: #0c4265;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  width: 100%;
  margin: auto;
  color :white;
  background: -webkit-gradient(linear, right bottom, left top, from(#252d2d),color-stop(82%, #000000));
  background: -webkit-linear-gradient(bottom right, #252d2d  0%,#000000 82%);
  background: -o-linear-gradient(bottom right, #252d2d  0%,#000000 82%);
  background: linear-gradient(to top left, #252d2d  0%,#000000 82%);
  -webkit-box-shadow: inset 0 0 10px #000000;
  box-shadow: inset 0 0 10px #000000;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 140px;
}

h1{
  font-size: 30px;
  padding:10px 0;
}

.quote{
  font-size: 24px;
  font-family: "Bree Serif";
}




