.fest{
    border-radius: 15px; 
    width: 100%;
    height: 500px;
    background: burlywood;
    position: relative;
    /* display: inline-flex; */
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.fest > video{
    width: 100%;
    display: block;
    position: absolute;
}
.fest-overlay{
    color: aliceblue;
    position: absolute;
    z-index: 10;
    display: -ms-flexbox;
    display: flex;
    width:100%;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: -webkit-radial-gradient(rgba(0,0,0,0.5),rgba(0,0,0,1));
    background: -o-radial-gradient(rgba(0,0,0,0.5),rgba(0,0,0,1));
    background: radial-gradient(rgba(0,0,0,0.5),rgba(0,0,0,1));
}


.blog-card {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 10px;
    -webkit-box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.9);
            box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.9);
    margin-bottom: 1.6%;
    background: rgba(0, 0, 0, 0.267);
    line-height: 1.4;
    font-family: sans-serif;
    width:50%;
    min-height: 200px;
    border-radius: 5px;
    overflow: hidden;
    z-index: 0;
  }
  .blog-card a {
    color: inherit;
  }
  .blog-card a:hover {
    color: #239ab4;
  }
  .blog-card:hover .photo {
    -webkit-transform: scale(1.1) rotate(3deg);
            -ms-transform: scale(1.1) rotate(3deg);
        transform: scale(1.1) rotate(3deg);
  }
  .blog-card.alt:hover .photo {
    -webkit-transform: scale(1.1) rotate(-3deg);
            -ms-transform: scale(1.1) rotate(-3deg);
        transform: scale(1.1) rotate(-3deg);
  }
  .blog-card .meta {
    position: relative;
    z-index: 0;
    height: 200px;
  }
  .blog-card .photo {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    transition: -webkit-transform .2s;
    -webkit-transition: -webkit-transform .2s;
    -o-transition: transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
  }
  .blog-card .details,
  .blog-card .details ul {
    margin: auto;
    padding: 0;
    list-style: none;
  }
  .blog-card .details {
    position: absolute;
    top: 0;
    bottom: 0;
    left: -100%;
    margin: auto;
    -webkit-transition: left .2s;
    -o-transition: left .2s;
    transition: left .2s;
    background: rgba(0, 0, 0, 0.267);
    color: #fff;
    padding: 10px;
    width: 100%;
    font-size: .9rem;
  }
  .blog-card .details a {
    -webkit-text-decoration: dotted underline;
            text-decoration: dotted underline;
  }
  .blog-card .details ul li {
    display: inline-block;
  }
  .blog-card .details .author:before {
    font-family: FontAwesome;
    margin-right: 10px;
    content: "\f007";
  }
  .blog-card .details .date:before {
    font-family: FontAwesome;
    margin-right: 10px;
    content: "\f133";
  }
  .blog-card .details .tags ul:before {
    font-family: FontAwesome;
    content: "\f02b";
    margin-right: 10px;
  }
  .blog-card .details .tags li {
    margin-right: 2px;
  }
  .blog-card .details .tags li:first-child {
    margin-left: -4px;
  }
  .blog-card .description {
    padding: 1rem;
    background: rgba(0, 0, 0, 0.267);
    position: relative;
    z-index: 1;
  }
  .blog-card .description h1,
  .blog-card .description h2 {
    font-family: Poppins, sans-serif;
  }
  .blog-card .description h1 {
    line-height: 1;
    margin: 0;
    font-size: 1.7rem;
  }
  .blog-card .description h2 {
    font-size: 1rem;
    font-weight: 300;
    text-transform: uppercase;
    color: #a2a2a2;
    margin-top: 5px;
  }
  .blog-card .description .read-more {
    text-align: right;
  }
  .blog-card .description .read-more a {
    color: #5ad67d;
    display: inline-block;
    position: relative;
  }
  .blog-card .description .read-more a:after {
    content: "\f061";
    font-family: FontAwesome;
    margin-left: -10px;
    opacity: 0;
    vertical-align: middle;
    -webkit-transition: margin .3s, opacity .3s;
    -o-transition: margin .3s, opacity .3s;
    transition: margin .3s, opacity .3s;
  }
  .blog-card .description .read-more a:hover:after {
    margin-left: 5px;
    opacity: 1;
  }
  .blog-card p {
    position: relative;
    margin: 1rem 0 0;
  }
  .blog-card p:first-of-type {
    margin-top: 1.25rem;
  }
  .blog-card p:first-of-type:before {
    content: "";
    position: absolute;
    height: 5px;
    background: #239ab4;
    width: 35px;
    top: -0.75rem;
    border-radius: 3px;
  }
  .blog-card:hover .details {
    left: 0%;
  }
  @media (min-width: 640px) {
    .blog-card {
      -ms-flex-direction: row;
          flex-direction: row;
      max-width: 600px;
    }
    .blog-card .meta {
      -ms-flex-preferred-size: 40%;
          flex-basis: 40%;
      height: auto;
    }
    .blog-card .description {
      -ms-flex-preferred-size: 60%;
          flex-basis: 60%;
    }
    .blog-card .description:before {
      -webkit-transform: skewX(-3deg);
              -ms-transform: skewX(-3deg);
          transform: skewX(-3deg);
      content: "";
      background: #239ab4;
      width: 30px;
      position: absolute;
      left: -10px;
      top: 0;
      bottom: 0;
      z-index: -1;
    }
    .blog-card.alt {
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
    }
    .blog-card.alt .description:before {
      left: inherit;
      right: -10px;
      -webkit-transform: skew(3deg);
              -ms-transform: skew(3deg);
          transform: skew(3deg);
    }
    .blog-card.alt .details {
      padding-left: 25px;
    }
  }


  .event-toggle{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding:5px;
  }
  .event-toggle>li{
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
  }

  .event-toggle>li>a{
    text-decoration: none;
    color: #fff;
    font-family: "Bree Serif";
  }
  .event-toggle>li>a:hover{
    color: #239ab4;
  }

  .event-toggle .active > a {
    color: #fff;
    padding:0 10px;
    -webkit-text-decoration: overline  #fff;
            text-decoration: overline  #fff;
    border-radius: 5%;
  }

  .event-groups{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-line-pack: center;
        align-content: center;
  }

@media only screen and (max-width: 720px) {
    .event-groups{
        display: block;
    }
    .blog-card {
        width: 90%;
    }
    .fest{
        height: 400px;
    }
    .fest > video {
        height: 100%;
        width: auto;
    }
    .fest-overlay img {
        width: 100%;
    }
}


